import styled from 'styled-components'
import SelectSearch from 'react-select-search'

const StyledReactSelectSearch = styled.div`

    .select-search-box {
        width: 100%;
        position: relative;
        margin-bottom: 20px;
        background: #fff;
        border-radius: 4px;
    }

    .select-search-box--multiple {
        border-radius: 4px;
        box-shadow: 0 7px 14px 0 rgba(50, 50, 93, 0.1), 0 3px 6px 0 rgba(0, 0, 0, 0.07);
        overflow: hidden;
    }
/*
    .select-search-box::after {
        font-family: fontawesome;
        content:"\f078";
        position: absolute;
        top: 0;
        right: 0;
        width: 40px;
        text-align: center;
        line-height: 50px;
        color: #222f3e;
        z-index: 1;
    }

    .select-search-box--multiple::after {
        content:"\f002";
    }
*/
    .select-search-box--input::after {
        display: none !important;
    }

    .select-search-box__out {
        display: none;
    }

    .select-search-box__search {
        display: block;
        width: 100%;
        height: 48px;
        border: none;
        background: none;
        outline: none;
        font-size: 16px;
        padding: 0 15px;
        color: #222f3e;
        -webkit-appearance: none;
        box-sizing: border-box;
        position: relative;
        z-index: 2;
        cursor: pointer;
        font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
        line-height: 48px;
        border: 2px solid #ccc;
    }

    input.select-search-box__search {
        line-height: 1;
    }

    .select-search-box--multiple .select-search-box__search {
        box-shadow: none;
    }

    .select-search-box--input .select-search-box__search {
        cursor: text;
    }

    .select-search-box__search:focus {
        cursor: text;
    }

    .select-search-box__search--placeholder {
        font-style: italic;
        font-weight: normal;
    }

    .select-search-box input::-webkit-input-placeholder {
        color: #ccc;
        font-style: italic;
        font-weight: normal;
    }

    .select-search-box input::-moz-placeholder {
        color: #ccc;
        font-style: italic;
        font-weight: normal;
    }

    .select-search-box input:-moz-placeholder {
        color: #ccc;
        font-style: italic;
        font-weight: normal;
    }

    .select-search-box input:-ms-input-placeholder {
        color: #ccc;
        font-style: italic;
        font-weight: normal;
    }

    input[type='search']::-webkit-search-cancel-button, input[type='search']::-webkit-search-decoration {
        -webkit-appearance: none;
    }

    .select-search-box__select {
        display: none;
        position: absolute;
        top: 49px;
        left: 0;
        right: 0;
        background: #fff;
        overflow: auto;
        box-shadow: 0 7px 14px 0 rgba(50, 50, 93, 0.1), 0 3px 6px 0 rgba(0, 0, 0, 0.07);
        z-index: 100;
        min-height: 49px;
    }

    .select-search-box--multiple .select-search-box__select {
        display: block;
        position: static;
        border-top: 1px solid #eee;
        border-radius: 0;
        box-shadow: none;
    }

    .select-search-box__select--display {
        display: block;
    }

    .select-search-box__options {
        margin: 0;
        padding: 0;
        list-style: none;
    }

    .select-search-box__option {
        font-size: 16px;
        font-weight: 400;
        color: #616b74;
        padding: 15px 20px;
        border-top: 1px solid #eee;
        cursor: pointer;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        user-select: none;
    }

    .select-search-box__option:first-child {
        border-top: none;
    }

    .select-search-box__option--hover, 
    .select-search-box__option:hover {
        background: #f4f7fa;
    }

    .select-search-box__option--selected {
        background: #54A0FF;
        color: #fff;
        border-top-color: #2184ff;
    }

    .select-search-box__option--selected:hover, 
    .select-search-box__option--selected.select-search-box__option--hover {
        background: #2184ff;
        color: #fff;
        border-top-color: #2184ff;
    }

    .select-search-box__group {
    }

    .select-search-box__group-header {
        background: #666;
        padding: 10px;
        color: #fff;
        text-transform: uppercase;
        text-align: center;
    }
`

export default ({ ...props }) => (
    <StyledReactSelectSearch className="col-sm-11 col-10 pr-0"> 
        <SelectSearch {...props} />
    </StyledReactSelectSearch>
)


