import Head from 'next/head'
import Layout from '../components/Layout'
import StyledSearchSelect from '../components/StyledReactSelectSearch'

import { EX_ELIGIBLE_GYMS, GYMS, POKESTOPS } from '../data/locationData'

const LOCATION_DATA = [
    {
        type: 'group',
        name: 'EX Eligible Gyms',
        items: EX_ELIGIBLE_GYMS
    },
    {
        type: 'group',
        name: 'Non-EX Eligible Gyms',
        items: GYMS
    },
    {
        type: 'group',
        name: 'Pokéstops',
        items: POKESTOPS
    },
]

export default class Locator extends React.Component {
    state = {
        mapLink: '',
    }
    handleMapLink = (e) => {
        e.preventDefault()
        window.open(e.target.href)
        this.setState({
            mapLink: '',
        })
    }
    handleSearchSelectChange = (value, state, props) => {
        this.setState({
            mapLink: 'https://www.google.com/maps/?q=' + value.value,
        })
    }
    render() {
        return(
            <Layout>
                <Head>
                    <title>Gym &amp; Pokéstop Locator | Pokémon GO Sydney City</title>
                </Head>
                <section>
                    <h1>Gym &amp; Pokéstop Locator</h1>
                    <div className="row">
                        <StyledSearchSelect 
                            search={true} 
                            multiple={false} 
                            options={LOCATION_DATA} 
                            autofocus={true} 
                            value="" 
                            name="locationName" 
                            placeholder="Name of Gym or Pokéstop"
                            onChange= {(value, state, props) => {this.handleSearchSelectChange(value, state, props)}}
                        />
                        <div className="col-sm-1 col-2 pl-0">
                            <a href={this.state.mapLink} 
                                target="_blank" 
                                onClick={this.handleMapLink} 
                                className={`btn btn-primary text-uppercase btn-lg rounded-0 ${this.state.mapLink === '' ? "disabled" : ""}`}>
                                Go
                            </a>
                        </div>
                    </div>
                    <p>To see all Gyms &amp; Pokéstops on a single Google Map, see <a href="https://www.google.com/maps/d/viewer?mid=1nu6kBCROyiYMHfFKnjypKy9IuIwFNsjv" target="_blank">coverage map</a>.</p>
                </section>
            </Layout>
        )
    }
}


